import Account from './Account.js';

export default class BankDatabase{
    constructor(){
        let firstAccount = new Account(12345, 1111, 2000, 2000);
        let secondAccount = new Account(67890, 2222, 3000, 3000);
        this.accounts = [firstAccount, secondAccount];
    }

    getAccountNumber(acNumber){
        let result;
        for(let i in this.accounts){
            if(this.accounts[i].getAccountNumber() === acNumber){
                result = this.accounts[i];
            }
        }
        return result;
    }

    authenticateUser(acNumber, pin){
        let account = this.getAccountNumber(acNumber);
        
        if(account !== undefined){
            return account.validatePin(pin);
        }else{
            return false;
        }
    }

    getBalance(accNumber){
        let account = this.getAccountNumber(accNumber);

        if(account !== undefined){
            return account.getBalance();
        }else{
            return 0;
        }
    }

    withdraw(accNumber, amount){
        let account = this.getAccountNumber(accNumber);
        
        if(account !== undefined){
            account.debit(amount);
            return account.getBalance();
        }else{
            return 0;
        }
    }
}


